import line from './../asset/line.svg'
import parking from './../asset/parking.svg'
import bus from './../asset/bus.svg'
import subway from './../asset/subway.svg'
import map from './../asset/map_img.png'

const Way = () =>{
    return(
        <section id="waybox" className="container-md text-center">
            <div className='imgbox'><img src={line} alt="" /></div>
            <h2><strong>  </strong></h2>
            <div id="waywrap" className="d-lg-flex justify-content-center align-items-center">
                <div class="mapimg"><img src={map} alt="" className='img-fluid' /></div>
                <div id="mapinfo">
                    <ul className='text-left'>
                        <li className='title'><img src={bus} class="icons" alt="" /><span></span></li>
                        <li className='content1'>  6   </li>
                        <li className='content2'>(500M  )</li>
                    </ul>
                    <ul className='text-left'>
                        <li className='title'><img src={subway} class="icons" alt="" /><span></span></li>
                        <li className='content1'>17, 17-1, 33, 720-1, 380,    AK</li>
                        <li className='content2'> </li>
                    </ul>
                    <ul className='text-left'>
                        <li className='title'><img src={parking} class="icons" alt=""/><span></span></li>
                        <li className='content1'>     </li>
                        <li className='content2'>. (2 )</li>
                    </ul>
                </div>
            </div>
        </section>
    )
}

export default Way