
import in_img1 from '../asset/in_img1.png'
import in_img2 from '../asset/in_img2.png'
import in_img3 from '../asset/in_img3.png'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination'
import 'swiper/css/autoplay'
import { Autoplay, Pagination } from 'swiper/modules';



const Event = () => {

    const eventdb = {
        icons:{
            ytb:{cls:"i-ytb"},
            insta:{cls:"i-ins"}
        },
        first : [{title1:" ", 
                  title2:" ",
                  txt1:"      !",
                  txt2:"       .",
                  txt3:"LP     ,   ",
                  txt4:"  ",
                  txt5:"",
                  txt6:"",
                  cls:"event-box"}
                  ],
        second : [{title1:" ", 
                  title2:" ",
                  txt1:" 1998 ,        ",
                  txt2:"  2020  2000 / 2010   TOP 10 ",
                  txt3:"      ,   ",
                  txt4:" 2020 <NAMMSE   >   ,",
                  txt5:"    LP  ",
                  txt6:"",
                  cls:"event-box2"}
                  ],
        third : [{title1:" ", 
                  title2:" ",
                  txt1:" 2005,   < Back To The Soul Flight > ",
                  txt2:" 2012,  1 < Principle of my Soul > ",
                  txt3:" 2015,  <      1st. > ",
                  txt4:" 2017,  <   > , <  >, < Baby Funk >,",
                  txt5:" 2018, < Sound Doctrine> ",
                  txt6:" 2023, < Soul Pop City >  / <    > ",
                  cls:"event-box2"}
                  ],
    }

    return(
        <>
        <section id="eve" className="container-md mb-0">
                    <Swiper
                    id="ulbox"
                    spaceBetween={0}
                    slidesPerView={1}
                    pagination={{ clickable: true }}
                    // autoplay={{
                    //     delay: 5000,
                    //     disableOnInteraction: false,
                    //   }}
                    modules={[Autoplay, Pagination]}
                    >
                        <SwiperSlide>
                            <div className="e_bg1">
                                <img src={in_img1} alt=" 1" className='img-fluid' />
                                <ul>
                                    {
                                        eventdb.first.map((v)=>{
                                            return(
                                                <>
                                                <li className={v.cls}><span className="title">{v.title1}</span><span className="spot">{v.title2}</span>
                                                    <div>{v.txt1}</div>
                                                    <div>{v.txt2}</div>
                                                    <div>{v.txt3}</div>
                                                    <div>{v.txt4}</div>
                                                    <div>{v.txt5}</div>
                                                </li>
                                                </>
                                            )
                                        })
                                    }
                                </ul>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="e_bg2">
                                <img src={in_img2} alt=" 2" className='img-fluid' />
                                <ul>
                                    {
                                        eventdb.second.map((v)=>{
                                            return(
                                                <>
                                                <li className={v.cls}><span className="title">{v.title1}</span><span className="spot">{v.title2}</span>
                                                    <div>{v.txt1}</div>
                                                    <div>{v.txt2}</div>
                                                    <div>{v.txt3}</div>
                                                    <div>{v.txt4}</div>
                                                    <div className='ms-3'>{v.txt5}</div>
                                                </li>
                                                </>
                                            )
                                        })
                                    }
                                </ul>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="e_bg3">
                                <img src={in_img3} alt=" 3" className='img-fluid' />
                                <ul>
                                    {
                                        eventdb.third.map((v)=>{
                                            return(
                                                <>
                                                <li className={v.cls}><span className="title">{v.title1}</span><span className="spot">{v.title2}</span>
                                                    <div>{v.txt1}</div>
                                                    <div>{v.txt2}</div>
                                                    <div>{v.txt3}</div>
                                                    <div>{v.txt4}</div>
                                                    <div>{v.txt5}</div>
                                                    <div>{v.txt6}</div>
                                                </li>
                                                </>
                                            )
                                        })
                                    }
                                </ul>
                            </div>
                        </SwiperSlide>
            </Swiper>
        </section>
        </>
    )
}

export default Event
